@use "forms";
@use "images";

$grey: #ddd;
$medium_grey: #aaa;
$width: 630px; // 42em
@mixin rounded($radius: 10px) {
  border-radius: $radius;
  -moz-border-radius-: $radius;
  -webkit-border-radius: $radius;
}

$small-font-size: 13px;
$text-font-size: 15px;
$title-font-size: 22px;

/*****************************************************************************/
/*
/* Common
/*
/*****************************************************************************/

/* Global Reset */

* {
  margin: 0;
  padding: 0;
}

// html, body {
//  height: 100%;
// }

body {
  background-color: #fefefe;
  font: $small-font-size helvetica, arial, clean, sans-serif;
  *font-size: small;
  text-align: center;
}

h1, h2, h3, h4, h5, h6 {
  font-size: $small-font-size * 1.2;
  margin: 1em 0;
}

p {
  margin: 1em 0;
}

a {
  color: #00a;
}

a:hover {
  color: black;
}

a:visited {
  color: #a0a;
}

table {
  font-size: inherit;
  font: 100%;
}

.align-right{
  text-align:right;
}

/*****************************************************************************/
/*
/* Home
/*
/*****************************************************************************/

ul.posts {
  list-style-type: none;
  margin-bottom: 2em;
  text-align: left;
}

ul.posts li {
  line-height: 1.75em;
}

ul.posts li {
  text-indent: -6rem;
  padding-left: 6rem;
}

ul.year-list.posts li {
  text-indent: -3em;
  padding-left: 3em;
}

.attribution{
  font-size: 100%;
  font-style: italic;
}

/*****************************************************************************/
/*
/* Site
/*
/*****************************************************************************/

.site {
  font-size: $text-font-size;
  text-align: justify;
  margin: 3em auto 2em auto;
  width: 87%;
  line-height: 1.5em;
}

/* #### Desktops #### */
@media screen and (min-width: 1024px){
  .site {
    width: $width;
  }
}

.site .slides{
  width: $width;
  height: calc($width * 3 / 4);
}

.title {
  color: #a00;
  font-weight: bold;
  margin-bottom: 1.5em;
}

.title .name{
  font-size: $title-font-size;
}

.site .title a {
  color: #a00;
  text-decoration: none;
}

.site .title a:hover {
  color: black;
}

.site .title a.extra {
  color: $medium_grey;
  text-decoration: none;
  margin-left: 1em;
}

.site .title a.extra:hover {
  color: black;
}

.site .meta {
  color: $medium_grey;
}

.site .footer {
  font-size: 80%;
  color: #666;
  border-top: 4px solid #eee;
  margin-top: 2em;
  overflow: hidden;
}

.site .footer .contact {
  float: left;
  margin-right: 3em;
}

.site .footer .contact a {
  color: #8085C1;
}

