/*****************************************************************************/
/*
/* Posts
/*
/*****************************************************************************/

#post {

}

/* standard */

#post pre {
  border: 1px solid $grey;
  background-color: #eef;
  padding: 0 .4em;
  margin: 1em 0;
}

#post ul,
#post ol {
  margin-left: 1.35em;
}

.highlight {
  overflow-x: scroll;
}

#post code {
  border: 1px solid #ddd;
  padding: 2px;
  background-color: #eef;
  font-size: 85%;
  @include rounded(4px);
}

#post pre {
  @include rounded(4px);
}

#post pre code {
  border: none;
  padding: 0px;
}

#post blockquote, .site blockquote {
  border-left: .4em solid $grey;
  padding-left: 1em;
  margin: 1em 0;
}

#related {
  margin-top: 2em;
}

#related h2 {
  margin-bottom: 1em;
}

/* Labels */

.small-muted {
  color: $medium_grey;
  font-family: Monaco, "Courier New", monospace;
  font-size: 80%;
}

.label {
  @include rounded(4px);
  font-size: 85%;
  background-color: $grey;
  padding-right: 4px;
  padding-left: 4px;
  padding-bottom: 2px;
  padding-top: 2px;
}

.nowrap {
  white-space: nowrap;
}

.text-left {
  text-align: left;
}

.video-border {
  border: 5px solid $grey;
}

.video-container {
  position: relative;
  padding-bottom: 62.5%;
  // padding-top: 30px;
  height: 0;
  overflow: hidden;
}

.video-container iframe,
.video-container object,
.video-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
